.haveAcc {
  text-align: center;
  width: 100%;
  padding-bottom: 20px;
  position: relative;
}
.haveAcc a {
  position: inherit;
  color: #348d60;
}
.haveAcc a:not(.fromWelcome) {
  transition: 0.2s;
  animation: pump linear infinite 10s;
  -webkit-animation: pump linear infinite 5s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
  -ms-transition: 0.2s;
  -o-transition: 0.2s;
}
@-webkit-keyframes pump {
  0% {
    top: 0;
  }
  2% {
    top: -500px;
  }
  4% {
    top: 20px;
  }
  5% {
    top: 0;
  }
}
@keyframes pump {
  0% {
    top: 0;
  }
  2% {
    top: -500px;
  }
  4% {
    top: 20px;
  }
  5% {
    top: 0;
  }
}
