span.loader {
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  border: 6px dotted white;
  border-top: none;
  width: 10px;
  height: 0px;
  -webkit-animation: spin 1s linear infinite alternate;
}
span.main-loader {
  animation: mainSpin 2s linear infinite;
  -webkit-animation: mainSpin 2s linear infinite;
  color: #fff;
  size: 2em;
  display: grid;
  place-items: centre;
}
@-webkit-keyframes mainSpin {
  to {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
  }
}
@keyframes mainSpin {
  to {
    transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
  }
}

div.walking-loader {
  position: relative;
  color: white;
  display: flex;
  align-items: flex-end;
}
div.walking-loader span:first-of-type {
  position: relative;
  animation: walk infinite linear 5s;
  -webkit-animation: walk infinite linear 5s;
  bottom: 0;
}
div.walking-loader span:last-of-type {
  position: relative;
  bottom: -5px;
  right: -65px;
}
span:last-of-type svg{
  height: 4em;
  width: 4em;
}
@-webkit-keyframes walk {
  from {
    right: 50px;
  }
  to {
    right: -70px;
  }
}
@keyframes walk {
  from {
    right: 50px;
  }
  to {
    right: -70px;
  }
}
@-webkit-keyframes spin {
  0% {
    transform: rotate(0deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    width: 5px;
  }
  50% {
    transform: rotate(180deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    height: 10px;
  }
  100% {
    width: 5px;
    transform: rotate(360deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -ms-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    width: 5px;
  }
  50% {
    transform: rotate(180deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotate(180deg);
    -moz-transform: rotate(180deg);
    -ms-transform: rotate(180deg);
    -o-transform: rotate(180deg);
    height: 10px;
  }
  100% {
    width: 5px;
    transform: rotate(360deg);
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -ms-transform: rotate(360deg);
    -o-transform: rotate(360deg);
  }
}



