* {
    font-family: 'Poppins', sans-serif;
}

.body {
    overflow-y: scroll;
   
}

/* variables */
:root {
    --ion-background-color: #ffffff;
    --ion-color-favorite: #348D63;
    --ion-color-btn: #FF5454;
    --ion-color-tabBtn: #000000;
    --ion-color-tabBtnHover: #ffffff;

}



/* subscriptions scroll */
ion-row {
    width: 1000px;
    
}


::-webkit-scrollbar {
    width: 0;  /* Remove scrollbar space */
    background: transparent;  /* Optional: just make scrollbar invisible */
}

ion-row {
    max-width: 200%;
    text-align:center
}

ion-col {
    height:60px;
    width: 30px;
    font-size: 8pt;
}


.scrollSubscriptions {
    height: 6em;
    overflow-x: scroll;
    max-width: 85%;
    float: left;
}

#viewAll {
    width: 15%;
    float: right;
    clear:right;
    border: none;
    background-color: var(--ion-color-favorite);
    height: 5em;
}

/* button color */
.ion-color-btn{
    --ion-color-base:var(--ion-color-btn);
}
 .icons {
     width: 25px;
     height: 25px;
 }


/* header */
.headerTitle {
    text-align: center;
}

/* highlights */
.highlightImg {
    margin: 0px;
    width: 90%;
    height: 90%;
    border-radius: 50%;
    /* box-shadow: 3px 6px 8px -1px rgba(0,0,0,0.4); */
}

.viewBtn {
    border: 0.5px solid #348D63;
    background-color: #348d638f;
    color: #ffffff;
    width: 20px;
}


/* announcements */

.ann-container {
    border: 1px solid rgba(0,0,0,0.1);
    margin: 10px;
    padding: 5px;
    border-radius: 10px;
    /* box-shadow: 3px 6px 8px -1px rgba(0,0,0,0.2); */
    overflow: hidden;
    clear: both;
}

h4 {
    margin-top: 10px;
    margin-bottom: 0px;
} 

h5 {
    margin-top: 5px;
    margin-bottom: 2px;
} 

.announceImg {
    margin: 5px;
    width: 28%;
    height: 30%;
    float: left;
    clear: none;
    border-radius: 10px;
    
}

.announceContent {
    margin: 2px 2px 0px 5px;
    float: left;
    clear: none;
    width: 65%;
}

.btn {
    border-radius: 40%;
    margin-top: 2px;
    margin: auto;
}

.fellowship {
    display: flex;
    box-shadow: 1px 1px 3px;
    margin: 10px;
    padding: 10px;
    justify-content: left;
    border-radius: 10px;
}

.fellowship img {
    width: 25%;
    height: 20%;
}

.fellowshipsContainer {
    overflow-y: scroll;
}

.fellowship h4 {
    min-height: 20px;
    margin: auto;
}

